.search-hotel {
  padding: 38px 18px 49px 18px;
}

.search-hotel__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
  margin: 0;
}

.search-hotel__description {
  text-transform: uppercase;
  text-align: center;
}

.search-hotel__link {
  display: block;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  padding: 16px 35px 16px 35px;
}
